.page-footer {
  margin-bottom: 98px;
  display: flex;
  flex-direction: column;
  align-self: center;
  max-width: 1366px;
  margin: 0 auto;

  h3 {
    font-family: $font-header;
    font-size: 30px;
    align-self: flex-end;
    padding-right: 50px;
  }

  a {
    text-decoration: none;
    font-family: $font-paragraph;
    color: #000000;
    font-size: 15px;
    align-self: flex-end;
    padding-right: 50px;

    &:hover {
      color: #c696d9;
    }

    &:active {
      color: #841aad;
    }
  }
}
