.page-header {
  display: flex;
  justify-content: space-between;
  box-sizing: border-box;
  padding-top: 0;
  margin: 0 auto;
  width: 100%;
  align-self: center;
  max-width: 1366px;
  font-family: $font-main;

  a {
    &:first-of-type {
      padding-left: 35px;
    }
  }
}

.page-header-cart {
  text-decoration: none;
  width: 90px;
  height: 74px;
  color: transparent;
  background-size: 90px 74px;
  background-repeat: no-repeat;
  padding-right: 35px;
  align-self: center;
  background-image: url("../img/icon-cart-unhappy.svg");

  &--full {
    background-image: url("../img/icon-cart-happy.svg");
    position: relative;

    &:hover {
      &::before {
        content: "Ваш товар уже в корзине";
        position: absolute;
        z-index: -1;
        width: 130px;
        bottom: -25px;
        right: 0;
        height: 25px;
        text-align: center;
        font-weight: 700;
        font-size: 15px;
        color: #000000;
        letter-spacing: 0.1em;
      }
    }
  }
}
