.page-main {
  display: flex;
  flex-direction: column;
  align-items: center;
  align-self: center;
  width: 100%;
  max-width: 1366px;
  margin: 0 auto;
  margin-top: 50px;
}

.page-main__nav {
  align-self: center;

  ul {
    display: flex;
    padding: 0;
    margin: 0;
    list-style: none;
    max-width: 330px;
    flex-wrap: wrap;

    li {
      &:nth-of-type(n+1) {
        margin-right: 20px;
      }

      a {
        text-decoration: none;
        font-family: $font-main;
        color: #000000;
        font-size: 30px;

        &:hover {
          color: #c696d9;
        }

        &:active {
          color: #841aad;
        }
      }
    }
  }

  .page-main__nav-best {
    a {
      color: #c696d9;
      font-weight: 700;
    }
  }
}

.page-main__product {
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  position: relative;
  padding-left: 50px;
  padding-right: 50px;
  box-sizing: border-box;
  width: 100%;

  .page-main__product-arrow {
    display: none;
  }

  button {
    border: none;
    color: transparent;
    background-image: url("../img/icon-cart.svg");
    width: 60px;
    height: 50px;
    background-size: 60px 50px;
    background-color: transparent;
    cursor: pointer;
    align-self: flex-start;
  }

  .page-main__product-arrow--down {
    display: block;
    margin: 30px;
    position: absolute;
    bottom: 55px;
    left: 50px;

    span {
      display: block;
      width: 15px;
      height: 15px;
      border-bottom: 5px solid #c696d9;
      border-right: 5px solid #c696d9;
      transform: rotate(45deg);
      margin: -10px;
      animation: arrow-down 2s infinite;

      &:nth-child(2){
        animation-delay: -0.2s;
      }

      &:nth-child(3){
        animation-delay: -0.4s;
      }
    }
  }

  @keyframes arrow-down {
    0%{
        opacity: 0;
        transform: rotate(45deg) translate(-20px,-20px);
    }
    50%{
        opacity: 1;
    }
    100%{
        opacity: 0;
        transform: rotate(45deg) translate(20px,20px);
    }
  }
}

.product {
  margin-top: 50px;
  display: flex;
  align-items: flex-start;
  font-family: $font-paragraph;
  font-size: 20px;
  width: 100%;

  h3 {
    font-family: $font-header;
    font-size: 30px;
  }

  .product__descr {
    max-width: 600px;
    text-align: left;
  }

  .product__price {
    font-size: 30px;
    color: #c696d9;
    font-weight: 700;
  }

  .product__priceold {
    position: relative;

    &::before {
      content: "";
      position: absolute;
      z-index: -1;
      width: 110px;
      top: 15px;
      left: 0;
      height: 2px;
      background-color: #ff3333;
      transform: rotate(8deg);
    }

    &::after {
      content: "";
      position: absolute;
      z-index: -1;
      width: 110px;
      top: 15px;
      left: 0;
      height: 2px;
      background-color: #ff3333;
      transform: rotate(-8deg);
    }
  }
}

.product__photos {
  display: flex;
  flex-direction: row-reverse;
  align-self: center;
  margin-left: 20px;

  img {
    cursor: pointer;

    &:first-of-type {
      margin-bottom: 44px;
    }
  }
}

.product__info {
  h3 {
    margin-top: 0;
  }
}