.rating-popup {
  position: fixed;
  overflow: auto;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: none;
  z-index: 50;
}

.rating-popup--opened {
  display: flex;
  justify-content: center;
}

.rating-popup__wrapper {
  align-self: center;
  box-sizing: border-box;
  width: 300px;
  min-height: 470px;
  display: flex;
  flex-direction: column;
  z-index: 500;
  background-color: #ffffff;
  position: relative;
  justify-content: center;
  border-radius: 4px;

  h6 {
    font-family: $font-header;
    font-size: 40px;
    color: #c696d9;
    align-self: flex-end;
    font-weight: 700;
    text-align: center;
    align-self: center;
    margin: 0;
    margin-top: 50px;
  }

  p {
    font-family: $font-paragraph;
    font-size: 20px;
    color: #000000;
    text-align: center;
    margin: 0;
    margin-top: 10px;
    align-self: center;
    margin-bottom: 10px;
  }

  form {
    display: flex;
    flex-direction: column;
    font-family: $font-paragraph;
  }

  label {
    margin-bottom: 10px;
  }

  input {
    margin-bottom: 10px;
    height: 25px;
    border-radius: 2px;
    padding-left: 3px;
    padding-right: 3px;
    box-sizing: border-box;
    font-size: 15px;
  }

  textarea {
    margin-bottom: 30px;
    border-radius: 2px;
    padding-left: 3px;
    padding-right: 3px;
    padding-top: 10px;
    padding-bottom: 10px;
    box-sizing: border-box;
    font-size: 15px;
  }
}

.rating-popup__wrapper-submit {
  align-self: center;
  width: 100%;
  height: 40px;
  border: none;
  background-color: #c696d9;
  color: #000000;
  font-family: $font-main;
  font-size: 20px;
  border-radius: 5px;
  font-weight: 700;
  letter-spacing: 0.2em;
  padding-top: 2px;
  box-sizing: border-box;
  cursor: pointer;

  &:hover {
    background-color: #841aad;
    color: #ffffff;
  }

  &:active {
    background-color: #841aad;
    color: #000000;
  }
}

.rating-popup__wrapper-close {
  position: absolute;
  top: 20px;
  right: 20px;
  height: 14px;
  width: 14px;
  border: none;
  padding: 0;
  background-color: transparent;
  background-image: url("../img/icon-cross.svg");
  background-repeat: no-repeat;
  background-size: 14px 14px;
  cursor: pointer;

  &:hover {
    -webkit-transform: scale(1.2);
    -ms-transform: scale(1.2);
    transform: scale(1.2);
  }

  &:focus {
    -webkit-transform: rotateZ(20deg);
    -ms-transform: rotateZ(20deg);
    transform: rotateZ(20deg);
  }
}