.popup {
  position: fixed;
  overflow: auto;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: none;
  z-index: 50;
}

.popup--opened {
  display: flex;
  justify-content: center;
}

.popup__wrapper {
  align-self: center;
  box-sizing: border-box;
  width: 600px;
  height: 100px;
  display: flex;
  flex-direction: column;
  z-index: 500;
  background-color: #ffffff;
  position: relative;
  justify-content: center;
  border-radius: 4px;

  p {
    font-family: $font-header;
    font-size: 40px;
    color: #c696d9;
    text-align: center;
    margin: 0;
    align-self: center;
    font-weight: 700;
  }
}