    display: flex;
    padding: 0;
    margin: 0;
    list-style: none;
    max-width: 330px;
    flex-wrap: wrap; }
    .page-main__nav ul li:nth-of-type(n+1) {
      margin-right: 20px; }
    .page-main__nav ul li a {
      text-decoration: none;
      font-family: "BebasNeueLight", "Arial Black", sans-serif;
      color: #000000;
      font-size: 30px; }
      .page-main__nav ul li a:hover {
