.page-main__product {
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  position: relative;
  padding-left: 50px;
  padding-right: 50px;
  box-sizing: border-box;
  width: 100%; }
  .page-main__product .page-main__product-arrow {
    display: none; }
  .page-main__product button {
    border: none;
    color: transparent;
    background-image: url("../img/icon-cart.svg");
    width: 60px;
    height: 50px;
    background-size: 60px 50px;
    background-color: transparent;
    cursor: pointer;
    align-self: flex-start; }
  .page-main__product .page-main__product-arrow--down {
    display: block;
    margin: 30px;
    position: absolute;
    bottom: 55px;
    left: 50px; }
    .page-main__product .page-main__product-arrow--down span {
      display: block;
      width: 15px;
      height: 15px;
      border-bottom: 5px solid #c696d9;
      border-right: 5px solid #c696d9;
      transform: rotate(45deg);
      margin: -10px;
      animation: arrow-down 2s infinite; }
      .page-main__product .page-main__product-arrow--down span:nth-child(2) {
        animation-delay: -0.2s; }
      .page-main__product .page-main__product-arrow--down span:nth-child(3) {
        animation-delay: -0.4s; }

@keyframes arrow-down {
  0% {
    opacity: 0;
    transform: rotate(45deg) translate(-20px, -20px); }
  50% {
    opacity: 1; }
  100% {
    opacity: 0;
    transform: rotate(45deg) translate(20px, 20px); } }

.product {
  margin-top: 50px;
  display: flex;
  align-items: flex-start;
  font-family: "OpenSans", "Arial Black", sans-serif;
  font-size: 20px;
  width: 100%; }
  .product h3 {
    font-family: "BebasNeueBold", "Arial Black", sans-serif;
    font-size: 30px; }
  .product .product__descr {
    max-width: 600px;
    text-align: left; }
  .product .product__price {
    font-size: 30px;
    color: #c696d9;
    font-weight: 700; }
  .product .product__priceold {
    position: relative; }
    .product .product__priceold::before {
      content: "";
      position: absolute;
      z-index: -1;
      width: 110px;
      top: 15px;
      left: 0;
      height: 2px;
      background-color: #ff3333;
      transform: rotate(8deg); }
    .product .product__priceold::after {
      content: "";
      position: absolute;
      z-index: -1;
      width: 110px;
      top: 15px;
      left: 0;
      height: 2px;
      background-color: #ff3333;
      transform: rotate(-8deg); }

.product__photos {
