.rating {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  padding-left: 50px;
  max-width: 1366px;
  margin: 0 auto;

  h5 {
    font-family: $font-header;
    font-size: 30px;
    align-self: flex-start;
    padding-right: 50px;
    margin: 0;
    margin-bottom: 10px;

    span {
      color: #c696d9;
      font-weight: 700;
      font-size: 20px;
    }
  }
}

.rating__wrapper {
  display: flex;
}

.rating__wrapper-item {
  width: 40px;
  height: 40px;
  background-color: white;
  border: 0.2px solid black;
  cursor: pointer;
  display: flex;
  justify-content: center;
}

.rating__wrapper-item.active {
  background-color: #c696d9;
  
}

.rating--disabled {
  pointer-events: none;
}